import React from 'react';
import { Row, Col, Card, Table, Modal, Button } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import IsEmpty from '../../App/components/Reusables/IsEmpty';
import Loader from '../../App/components/Reusables/Loader';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import { getTaxes, deleteTaxes } from '../../api/misc';
import { getTaxesSearchFilterObject } from '../../helpers/getSearchFilterObject';
import TaxesFilter from '../../App/components/SearchFilters/TaxesFilter';
import { getStartEnd } from '../../helpers/getStartEnd';

const Taxes = (props) => {
    const [taxesArr, setTaxesArr] = React.useState([]);
    const [loading, setLoading] = React.useState(true)
    const [deleteData, setDeleteData] = React.useState({ showModal: false, taxId: null });
    const [instanceCount, setInstanceCount] = React.useState(0)

    React.useEffect(() => {
        const controller = new AbortController();
        getTaxes({
            "action": "get",
            "filter": "none",
            ...getTaxesSearchFilterObject(props),
            ...getStartEnd(props),
        }, controller.signal)
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    console.log(res.json.taxes)
                    setTaxesArr(res.json.taxes);
                    setInstanceCount(res.json.count)
                    setLoading(false);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch((err) => {
                console.log(err)
            });

        return () => controller.abort();
    }, []);



    const _triggerDeleteModal = (id) => {
        setDeleteData({ showModal: true, taxId: id });
    }

    const _delete = (idToBeDeleted) => {
        deleteTaxes({ action: 'delete', taxes_id: [idToBeDeleted] })
            .then(async (res) => {
                console.log(res.json)
                if (res.json.status) {
                    const deleted = taxesArr.find(instance => instance.id === idToBeDeleted);
                    let tempArr = taxesArr;
                    tempArr.splice(tempArr.indexOf(deleted), 1);
                    await setTaxesArr([...tempArr])
                    await setDeleteData({ ...setDeleteData, showModal: false, taxId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Tax deleted successfully!', background: '#dffff0' })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch((err) => {
                console.log(err)
            });
    }


    const TaxesList = taxesArr.map((tax, index) => {
        return (
            <tr key={index}>
                <th scope="row">{tax.id}</th>
                <td
                    onClick={() => {
                        props.history.push('/tax/' + tax.id)
                    }}
                    style={{ cursor: 'pointer', color: '#55588b' }}>{tax.name}</td>
                <td>{tax.rate}</td>
                <td>{tax.tax_type}</td>
                <td>{tax.code}</td>
                <td>{tax.is_active ? 'Yes' : 'No'}</td>
                <td>
                    <i
                        className="feather icon-edit text-c-green f-19 m-r-10"
                        style={{ cursor: 'pointer' }}
                        onClick={() => props.history.push('/tax/update/' + tax.id)}
                    />
                    <i
                        className="feather icon-trash-2 text-c-red f-19 m-r-5"
                        style={{ cursor: 'pointer' }}
                        onClick={() => _triggerDeleteModal(tax.id)}
                    />
                </td>
            </tr>
        )
    })

    return (
        <Aux>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10">Taxes</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`tax/create`)}
                    > <i className="fa fa-plus text-c-white f-15 m-r-5" />Add New</button>
                </div>
            </Row>
            <Row>
                <Col>

                    <Card>
                        <Card.Body>
                            <TaxesFilter />
                            <Table striped responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Name</th>
                                        <th>Rate</th>
                                        <th>Tax Type</th>
                                        <th>Tax Code</th>
                                        <th>Enabled</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {TaxesList || ''}
                                </tbody>
                            </Table>
                            {isEmpty(taxesArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                            {loading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />

                        </Card.Body>
                    </Card >


                    <DeleteModal
                        show={deleteData.showModal}
                        onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                        onDelete={() => _delete(deleteData.taxId)}
                    />


                </Col>
            </Row>
        </Aux>
    );
}

export default Taxes;